import React from "react";

export default ({ data }) => 
  <div>
    <h1>
      About {data.site.siteMetadata.title}
    </h1>
    <p>
      This is a quick blog made with Gatsby, a static site generator for React which utilizes graphql during its build process.
    </p>
    <p>
      Life is easier when things are 100% transparent. I don't understand why people speak in circles; it is so simple to be straightforward all the time.
      I like to hack together small applications, practice martial arts, dance, and ride my motorcycle.
    </p>
  </div>

export const query = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`



